import React, { Component } from 'react';
import { Input, DatePicker, Button} from 'antd';
// import styles from './index.module.less';

export default class Filter extends Component {
    state = {
        nodeTaskName:"",//
        receiveDate:[],//
    }
    handleChangeInput = (e) => {
        this.setState({
            nodeTaskName:e.target.value
        })
    }
    handleChangeDate = (dates) => {
        this.setState({
            receiveDate:dates
        })
    }
    _reset = () => {
        this.setState({
            nodeTaskName:"",
            receiveDate:[]
        });
        this.props.changeFilterValue({
            nodeTaskName:"",
            receiveDate:[]
        })
    }
    _search = () => {
        this.props.changeFilterValue({
            nodeTaskName:this.state.nodeTaskName,
            receiveDate:this.state.receiveDate
        })
    }
    render() {
        return (
            <React.Fragment >
                <div className='filter-item'>
                    <Input style={{width:220}} placeholder="" value={this.state.nodeTaskName} onChange={this.handleChangeInput}/>
                </div>
                <div className='filter-item'>
                    <DatePicker.RangePicker  style={{width:320}} value={this.state.receiveDate} format={"YYYY-MM-DD"} onChange={this.handleChangeDate}/>
                </div>
                <div className='filter-item'>
                    <Button ghost type="primary" icon="search" style={{marginRight:12}} onClick={this._search}></Button>
                    <Button icon="sync" style={{marginRight:12,background:"#24695C",color:"#fff"}} onClick={this._reset}></Button>
                </div>
            </React.Fragment>
        )
    }
}
