import React, { Component } from 'react';
import { getAction,postAction } from '@/axios';
import { message, Table, Steps, Popover, Timeline,Divider,Modal} from 'antd';
import moment from 'moment';
import { withRouter } from 'react-router-dom';
import PreviewDoc from '@/pages/common/PreviewDoc';

//
const auditStatusList = [
    {color:"#52C41A",text:""},
    {color:"#FA541C",text:""},
    {color:"#52C41A",text:""},
    {color:"orange",text:""}
];
function findStatus(text){
    let arr = auditStatusList.find(item => item.text===text);
    if(arr){
        return arr
    }
    return false;
}
// const expandSteps = (record, index, indent, expanded) => {
const expandSteps = (data) => {        
    let stepsData = data || {current:0,steps:[]};
    // console.log(stepsData);
    if(stepsData.steps.length === 0){
        return <div style={{textAlign:"center"}}></div>
    }
    return (
        <Steps current={stepsData.current} progressDot={true}>
            {
                stepsData.steps.map((item,index) => (
                    <Steps.Step key={index} title={item.title} description={
                        <Popover
                            content={
                                <Timeline>
                                    {
                                        item.toolTips?item.toolTips.map((el,index) => (
                                            <Timeline.Item key={index}>
                                                <p style={{color:"rgba(0,0,0,0.65)"}}>
                                                    {el.job}
                                                    <span style={{color:"#000000",padding:"0 5px"}}>{el.name}</span>
                                                    {
                                                        findStatus(el.status)?<span style={{color:findStatus(el.status).color}}>{findStatus(el.status).text}</span>:""
                                                    }
                                                </p>
                                                {
                                                    (el.status==='' || el.status==='')?
                                                    <>
                                                        <p style={{color:"rgba(0,0,0,0.65)"}}><span>{el.comments || '()'}</span></p>
                                                        <p style={{color:"rgba(0,0,0,0.65)"}}>{moment.unix(el.time/1000).format("YYYY/MM/DD HH:mm:ss")}</p>
                                                    </>:null
                                                } 
                                            </Timeline.Item>
                                        )):null
                                    }
                                </Timeline>
                            }
                        >
                            <span style={{cursor:"pointer",color:"#3F54A2"}}>{item.detail}{item.detail?item.approveType===1?'()':'()':''}</span>
                        </Popover>
                    } />
                ))
            }
        </Steps>
    )
};

const statusList = [
    {label:"",value:1,color:"#FA541C"},
    {label:"",value:2,color:"rgba(65,100,228,1)"},
    {label:"",value:3,color:"rgba(250,84,28,0.3)"},
    {label:"",value:4,color:"rgba(250,140,22,0.3)"},
    {label:"",value:5,color:"rgba(82,196,26,1)"},
    {label:"",value:5,color:"rgba(82,196,26,1)"},
];

class TodoItems extends Component {
    constructor(props){
        super(props);
        this.state = {
            nodeTaskName:"",//
            receiveDate:[],//
            itemInfo:{},//
            expandTableRowKeys:[],//key
            queryParams:{},
            columns:[
                {
                    title:"",
                    dataIndex:"sortIndex",
                    width:"80px"
                },{
                    title:"",
                    dataIndex:"nodeTaskName",
                    width:"360px"
                },{
                    title:"",
                    dataIndex:"taskStartTime",
                    width:"160px",
                    // render:(text)=>{
                    //     return moment(text).format("YYYY-MM-DD")||"";
                    // }
                },{
                    title:"",
                    dataIndex:"taskEndTime",
                    width:"160px",
                    // render:(text)=>{
                    //     return moment(text).format("YYYY-MM-DD")||"";
                    // }
                },{
                    title:"",
                    dataIndex:"status",
                    width:"130px",
                    render:(text) => {
                        const statusInfo = statusList.find(item => item.label === text);
                        return statusInfo?<React.Fragment><i style={{display:"inline-block",width:8,height:8,borderRadius:"50%",marginRight:10,background:statusInfo.color}}></i>{statusInfo.label}</React.Fragment>:""
                    }
                },{
                    title:"",
                    dataIndex:"userConclusionsType",
                    width:"80px",
                    render:(text) => <span style={{color:text===""?"#F5222D":""}}>{text}</span>
                },{
                    title:"",
                    width:"180px",
                    align:'center',
                    render:(text,record) => {                        
                        return(
                            <React.Fragment>
                                <span className='link' onClick={()=>{this.goToHandle(record)}}></span>
                                <Divider type="vertical"/>
                                {
                                    this.state.expandTableRowKeys.length>0 && this.state.current.taskId === record.taskId?
                                    <span className='link' onClick={()=>this.onExpandTable(false,record)}></span>:
                                    <span className='link' onClick={()=>this.onExpandTable(true,record)}></span>
                                }
                                
                            </React.Fragment>
                        )
                    }
                }
            ],
            dataSource:[],
            pagination:{
                current:1,
                pageSize:5,
                total:0,
                showTotal:(total)=>`${total}`,
                showQuickJumper:true,
                // showSizeChanger:true,
                size:"small"
            },
            current:null,
            businessId:"",//id,
            showItem:{},
        }
    }
    componentDidMount(){
        // console.log("didmount");
        this.queryOverTaskList();
        document.addEventListener("keydown",this._onkeydown);
    }
    componentDidUpdate(preProps){
        if(this.props.activeKey==="1"){
            if(preProps.activeKey!=="1"){//
                // console.log("");
                this.queryOverTaskList();
            }
            if(JSON.stringify(preProps.filterValue)!==JSON.stringify(this.props.filterValue)){
                // console.log(",");
                this.queryOverTaskList();
            }
        }
    }
    _onkeydown = (e) => {
        if(e.keyCode === 13){
            this.queryOverTaskList();
        }
    }
    componentWillUnmount(){
        document.removeEventListener("keydown",this._onkeydown)
    }
    //
    queryOverTaskList(page){
        let filterValue = this.props.filterValue;
        let taskStartTimeStart = "",taskStartTimeEnd = "";
        if(filterValue.receiveDate.length>0){
            taskStartTimeStart = moment(filterValue.receiveDate[0]).format("YYYY-MM-DD HH:mm:ss")||"";
            taskStartTimeEnd=moment(filterValue.receiveDate[1]).format("YYYY-MM-DD HH:mm:ss")||"";
        }
        let data = {
            taskStartTimeStart,
            taskStartTimeEnd,
            keyWord:filterValue.nodeTaskName
        }
        let current = 1;
        if(page==="page"){
            current = this.state.pagination.current;
        }
        let url = `/ybt/wf/approveTask/queryOverTaskList?pageNum=${current}&pageSize=${this.state.pagination.pageSize}`;
        postAction(url,data).then(res => {
            if(res.success){
                //
                let pagination = JSON.parse(JSON.stringify(this.state.pagination));
                pagination.total = res.obj.total;
                pagination.current = res.obj.page;
                pagination.pageSize = res.obj.pageSize;

                //
                let dataSource = res.obj.rows||[];
                dataSource.forEach((item,index)=>{
                     item.sortIndex = index+1;
                });
                this.setState({
                    dataSource,
                    pagination
                })
                if(page!=="page"){
                    this.onExpandTable(true,dataSource[0])
                }
            }else{
                message.error(res.obj)
            }
        })
    }
    //
    onExpandTable = (expanded, record) => {
        if(record){
            if(expanded){
                this.getExpandedRowsDetail(record,record.sortIndex-1);
            }
            this.setState({
                expandTableRowKeys:expanded?[record.sortIndex]:[],
                current:record
            })
        }
    }
    //
    goToHandle = (record) => {
        //1---
        //2---formid
        //3---formtaskid
        //4--formtaskid
        //5--
        //6---formid
        if(record.nodeTaskType===1 || record.nodeTaskType===5){//
            this.props.history.push("/auditManagement/lookUp?id="+record.processNodeTaskId)
        }else{
            this.setState({
                showPDF:true,
                businessId:record.businessId,//id
                showItem:record
            })
            // //processtype=2
            // Modal.confirm({
            //     title:null,
            //     icon:null,
            //     content:<iframe scrolling='auto' title='' style={{border:'none',width:'100%',height:'500px'}} src={`http://${window.location.host}/ybt/form/dl/result/view?${record.processType === 2 ? 'formId':'formTaskId'}=${record.businessId}`}></iframe>,
            //     okText:'',
            //     cancelText:"",
            //     onCancel:()=>{
            //         window.open(`http://${window.location.host}/ybt/form/dl/export?formTaskId=${record.taskId}`)
            //     },
            //     width:1000,
            // })
        }
        
        // this.props.history.push("/auditManagement/audit?id="+record.process_node_task_id)
        //
    }
    //
    getExpandedRowsDetail=(record,index)=>{ 
        // console.log(record);
        let data={
            taskId:record.taskId,
            processId:record.processId
        }
        getAction('/ybt/wf/approveTask/queryOverTaskProcess',data).then(res => {
            if(res.success){
                let dataSource = JSON.parse(JSON.stringify(this.state.dataSource));
                //setps
                let stepsData = {current:res.obj.current,steps:res.obj.setps}
                dataSource[index].stepsData =stepsData;
                this.setState({
                    dataSource
                });
            }else{
                message.error(res.obj)
            }
        })
    }
    //
    onChange=(pagination, filters, sorter)=>{
        //  console.log(pagination, filters, sorter);
        this.setState({
            pagination
        },()=>{
            this.queryOverTaskList("page");
        })
    }
    render() {
        return (
            <div className='homePage-items-container'>
                    <div className='card'>
                        <Table
                            rowKey="sortIndex"
                            columns={this.state.columns}
                            dataSource={this.state.dataSource}
                            expandedRowRender={record => expandSteps(record.stepsData)}//
                            expandedRowKeys={this.state.expandTableRowKeys}//
                            onExpand={this.onExpandTable}//
                            pagination={this.state.pagination}
                            onChange={this.onChange}
                        />
                    </div>
                    <Modal
                        visible={this.state.showPDF}
                        // title=""
                        title={<span style={{fontWeight:"bold"}}></span>}
                        width={1200}
                        bodyStyle={{padding:0,height:"70vh"}}
                        destroyOnClose={true}
                        footer={false}
                        onCancel={()=>{this.setState({showPDF:false})}}
                    >
                        <PreviewDoc id={this.state.showItem.businessId} type={this.state.showItem.nodeTaskType===2||this.state.showItem.nodeTaskType===6?'formId':'formTaskId'}/>
                    </Modal>
            </div>
        )
    }
}

export default withRouter(TodoItems)