import React from 'react';
import { Tabs } from 'antd';
import './index.less'
import TodoItems from './todoItems';
import DoneItems from './doneItems';
import Filter from './filter';

const { TabPane } = Tabs;

// const colorStyle1 = { backgroundColor: '#C08E60', marginLeft: "10px", marginTop: "-5px" };
// const colorStyle2 = { backgroundColor: 'rgba(0,0,0,0.25)', marginLeft: "10px", marginTop: "-5px" };

class Items extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            activeKey: '0',
            filterValue:{
                nodeTaskName:"",//
                receiveDate:[],//
            }
        }
        this.filterRef = React.createRef();
    }
    tabChange = (activeKey) => {
        this.setState({
            activeKey
        });
        this.filterRef.current._reset();
    }
    _changeFilterValue = (filterValue) => {
        this.setState({
            filterValue:{
                nodeTaskName:filterValue.nodeTaskName,
                receiveDate:filterValue.receiveDate
            }
        })
    }
    render(){
        return (
            <div className="items-container">
                <Tabs 
                    activeKey={this.state.activeKey} 
                    onChange={this.tabChange} 
                    className='homePage-items-tabs' 
                    forceRender={false}
                    tabBarExtraContent={<Filter ref={this.filterRef} changeFilterValue={this._changeFilterValue}/>}
                >
                    <TabPane key="0" tab="">
                        <TodoItems filterValue={this.state.filterValue} activeKey={this.state.activeKey}/>
                    </TabPane>
                    <TabPane key="1" tab="">
                        <DoneItems filterValue={this.state.filterValue} activeKey={this.state.activeKey}/>
                    </TabPane>
                </Tabs>
            </div>
        )
    }
}

export default Items