import React, { Component } from 'react';
import { Icon,Modal,message } from 'antd';
import { getAction } from '@/axios';

import NoImage from './default-user.png';

export default class InfoCard extends Component {
    state = {
        departName:"",
        name:"",
        userCode:"",
        positionName:"",
        src:NoImage,
        isPicAuth:true
    }
    componentDidMount(){
        let userInfo=localStorage.getItem('leftMenuList')
        if(userInfo){
            userInfo=JSON.parse(userInfo)
            this.setState({
                departName:userInfo.departName,
                name:userInfo.name,
                userCode:userInfo.userCode,
                positionName:userInfo.positionName
            },this._getPicture)
        }
    }
    _loginout = () => {
        Modal.confirm({
            title: '',
            // content: '',
            okText: '',
            cancelText: '',
            onOk() {
                getAction('/ybt/auth/logout').then(res=>{
                    //
                    if(res.success){
                        if(window.localStorage.getItem("isSsoAuth")==="true"){
                            //,
                            localStorage.clear();
                            getAction("/ybt/sso/casServerLogOutUrl").then(res=>{
                                if(res.success){
                                    message.success("");
                                    window.location.href = res.obj
                                }
                            })
                        }else{
                            localStorage.clear();
                            message.success("");
                            window.location.href = window.location.origin;
                        }
                    }else{
                        message.error("");
                    }
                })
            }
        })
    }
    _getPicture = () => {
        getAction("/ybt/system/setting/auth/picture").then(res=>{
            if(res.success){
                if(res.obj && res.obj.isPicAuth===1){
                    this.setState({
                        src:`/ybt/system/setting/picture?userCode=${this.state.userCode}`
                    })
                }
            }else{
                message.error(res.obj)
            }
        })
    }
    _ImageError = () => {
        this.setState({
            src:NoImage
        })
    }
    render() {
        return (
            <div className="info-card">
                <Icon type="poweroff" onClick={this._loginout}/>
                <img alt="" src={this.state.src} onError={this._ImageError}/>
                <p style={{margin:"14px 0 0 0",fontSize:"20px",fontWeight:"bold"}}>{this.state.name}</p>
                <p style={{margin:"5px 0 5px 0"}}>{this.state.userCode}</p>
                <div style={{display:"inline-block",textAlign:"left",margin:"0 25px"}}>
                    <p style={{margin:"8px 0px"}}>{this.state.departName}</p>
                    <p>{this.state.positionName || "()"}</p>
                </div>
            </div>
        )
    }
}
