import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';

// import {getAction} from '@/axios'
// import { message } from 'antd';

// let moduleList = ["myFillingTask","dataCenter","fillPlan","formManagement","auditManagement","summaryRecordManagement","dataManagement","systemSettings"];
let moduleList = ["myFillingTask","fillPlan","formManagement","auditManagement","summaryRecordManagement","dataManagement","systemSettings"];
var changeSizeTimer = null;
// const debounce = (fn,wait) => {
