class TopModule extends Component {
    state={
        moduleWidth:189,//module15px
        showNum:8,
        currentPage:1,
        menus:[
             // "/fillPlan","/myFillingTask","/dataCenter","/auditManagement","/formManagement","/defectManagement","/dataManagement","/systemSettings","/newscenter"
        ],
    }
    componentDidMount(){
        let menus = [];
        //
        try {
            let localMenus = JSON.parse(window.localStorage.getItem("leftMenuList")).menus;
            localMenus.forEach(item => {
                if(item.url !=="/systemSettings" || item.url !=="/dataManagement"){///menus
                    menus.push(item.url)
                }else{
                    if(item.subMenus.length>0){//menus
                        menus.push(item.url)
                    }
                }
            });
            this.setState({
                menus
            })
        } catch (error) {
            console.log(error)
        }
        // console.log(this.container.clientWidth);
        let index = Math.floor(this.container.clientWidth/this.state.moduleWidth);
        this.setState({
            showNum:index
        })
        window.addEventListener('resize',this.changeSize)
    }
    changeSize = () => {
        //
        if (changeSizeTimer) {
            clearTimeout(changeSizeTimer);
            changeSizeTimer = null;
        }
        changeSizeTimer = setTimeout(() => {
            let index = Math.floor(this.container.clientWidth/this.state.moduleWidth);
            this.setState({
                showNum:index
            })
        }, 500)
    }
    componentWillUnmount(){
        window.removeEventListener('resize',this.changeSize)
    }
    componentDidUpdate(preProps){
        if(preProps.collapsed!==this.props.collapsed){
            let index = Math.floor(this.container.clientWidth/this.state.moduleWidth);
            this.setState({
                showNum:index
            })
        }
    }
    
    //
    _inMenus = (url) => {
        return this.state.menus.indexOf(`/${url}`)===-1?false:true
    }
    _jumpToModule = (url) => {
        const inMenus = this._inMenus(url);
        if(inMenus){
            // if(url==='dataCenter'){
            //     getAction('/ybt/auth/getToken').then(res=>{
            //         if(res.success){
            //             let token = res.obj;
            //             window.open(`http://10.175.205.20:8880/#/YBTSSO?token=${token}`)
            //         }else{
            //             message.error(res.obj)
            //         }
            //     })
            // }else{
            //     this.props.history.push(`/${url}`);
            // }
            this.props.history.push(`/${url}`);
        }
    }
    _changePage = (page) => {
        this.setState({currentPage:page})
    }

    render() {
        let page1 = moduleList.slice(0,this.state.showNum);
        let page2 = moduleList.slice(this.state.showNum);
        return (
            <div className="modules-container" ref={(container)=>this.container = container }>
                <div style={this.state.currentPage===1?{}:{display:"none"}} className="module-page">
                    {page1.map(item=>{
                        if(!this._inMenus(item)){
                            return <div className={`module disable ${item}`} key={item} onClick={()=>{this._jumpToModule(item)}}></div>
                        }else{
                            return <div className={`module ${item}`} key={item} onClick={()=>{this._jumpToModule(item)}}></div>
                        }
                    })}
                </div>
                <div style={this.state.currentPage===1?{display:"none"}:{}} className="module-page">
                    {page2.map(item=>{
                        if(!this._inMenus(item)){
                            return <div className={`module disable ${item}`} key={item} onClick={()=>{this._jumpToModule(item)}}></div>
                        }else{
                            return <div className={`module ${item}`} key={item} onClick={()=>{this._jumpToModule(item)}}></div>
                        }
                    })}
                </div>
                <div className="toggle-button" style={this.state.showNum<moduleList.length?{}:{display:"none"}}>
                    <span className={this.state.currentPage===1?"active":""} onClick={()=>{this._changePage(1)}}></span>
                    <span className={this.state.currentPage===1?"":"active"} onClick={()=>{this._changePage(2)}}></span>
                </div>
            </div>
        )
    }
}

export default withRouter(TopModule);