import React, { Component } from 'react';
import { withRouter } from 'react-router-dom'
import { postAction } from '@/axios';
import { message } from 'antd';
import NoteDetailModal from './noteDetailModal';

class Notes extends Component {
    constructor(props){
        super(props)
        this.state = {
            messageList:[
                // {readStatus:"",messageContent:"111",messageId:"1",createTime: '2018-03-20'},
            ],
            showDetailModal:false,
        }
    }
    componentDidMount(){
        this.getMessageList();
    }
    //  
    getMessageList=(props)=>{
        let data = {index:1};//1
        postAction('/ybt/message/getMessageList?pageNum=1&pageSize=5',data).then(res => {
            if(res.success){
                this.setState({
                    messageList:res.obj.rows
                })
            }else{
                message.error(res.obj)
            }
        })
    }
    //
    toNewsCenter=(messageId)=>{
        if(messageId){
            this.props.history.push("/newscenter?id="+messageId);
        }else{
            this.props.history.push("/newscenter");
        }
    }
    //
    showDetail = (messageId) => {
        this.setState({showDetailModal:true,messageId})
    }
    //
    hideDetailModal = () => {
        this.setState({showDetailModal:false},()=>{
            this.getMessageList();
        })
    }
    render() {
        return (
            <div className='info-note'>
                <div className='title'>
                     
                    <span title='' style={{fontSize: "24px", color: '#3F54A2', cursor:"pointer", float: "right", lineHeight: '14px', marginTop: '-6px', marginRight: "10px"}} onClick={this.toNewsCenter.bind(this,"")}>...</span>
                </div>
                <ul className='NewsList'>
                    {
                        this.state.messageList.length?this.state.messageList.map(item => (
                            <li key={item.messageId} onClick={()=>{this.showDetail(item.messageId)}}>
                                <p title={item.messageContent} className={item.readStatus===""?"readStatus":""}> <span>{item.readStatus===""?"":""}</span> {item.messageContent}</p>
                                <p>{item.createTime}</p>
                            </li>
                        )):null
                    }
                </ul>
                {
                    this.state.showDetailModal
                    ?
                    <NoteDetailModal hideDetails = {this.hideDetailModal} messageId = {this.state.messageId} />
                    :
                    null
                }
            </div>
        )
    }
}


export default withRouter(Notes);