import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import { getAction } from '@/axios';

class Items extends Component {
    state = {
        todoBusiness:0,//
        doneBusiness:0,//
    }
    componentDidMount(){
        this.getTaskNumber();
    }
    //
    getTaskNumber=()=>{
        getAction('ybt/wf/approveTask/getTaskNumber',{}).then(res => {
            if(res.success){
                this.setState({
                    doneBusiness:res.obj[0],
                    todoBusiness:res.obj[1]
                })
            }else{
                // message.error(res.obj)
            }
        })
    }
    render() {
        return (
            <div className="item-card">
                <div className="part" style={{left:"-15px"}}>
                    <span style={{color:"#C08E60"}}>{this.state.todoBusiness}</span>
                    <p></p>
                </div>
                <div className="split-line"></div>
                <div className="part" style={{left:"15px"}}>
                    <span style={{color:"#839C97"}}>{this.state.doneBusiness}</span>
                    <p></p>
                </div>
            </div>
        )
    }
}


export default withRouter(Items);